import React, { useState, useEffect, useContext } from "react";
import { Link, useHistory } from "react-router-dom";
import { Auth } from "../../services/amplify";

import { Navbar, Nav, NavDropdown, Container, Spinner } from "react-bootstrap";
import { BsPersonCircle } from "react-icons/bs";

import NavbarLogo from "../../assets/logo_3.png";

import AuthContext from "../../contexts/auth";

import "./styles.css";

function Menu(props) {
  const history = useHistory();

  const [vendasDrop, setVendasDrop] = useState(false);
  const [comprasDrop, setComprasDrop] = useState(false);
  const [expedicaoDrop, setExpedicaoDrop] = useState(false);
  const [relatoriosDrop, setRelatoriosDrop] = useState(false);
  const [cadastrosDrop, setCadastrosDrops] = useState(false);
  const [userDrop, setUserDrop] = useState(false);

  const [vendasActive, setVendasActive] = useState(false);
  const [comprasActive, setComprasActive] = useState(false);
  const [expedicaoActive, setExpedicaoActive] = useState(false);
  const [relatoriosActive, setRelatoriosActive] = useState(false);
  const [cadastrosActive, setCadastrosActive] = useState(false);

  const [carregandoLogout, setCarregandoLogout] = useState(false);

  const [userName, setUserName] = useState("");

  const userContext = useContext(AuthContext);

  useEffect(() => {
    const pathname = window.location.pathname;

    if (pathname.includes("vendas")) setVendasActive(true);
    if (pathname.includes("compras")) setComprasActive(true);
    if (pathname.includes("aprovarpedidos")) setExpedicaoActive(true);
    if (pathname.includes("relatorio")) setRelatoriosActive(true);
    if (pathname.includes("prospeccao")) setVendasActive(true);
    if (pathname.includes("produtos")) setCadastrosActive(true);

    // Debug
    // Exibir o objeto recebido via contexto
    console.log(userContext);

    if (userContext) {
      setUserName(userContext.attributes["custom:displayname"]);
    } else {
      setUserName("Convidado");
    }
  }, []);

  const signOut = () => {
    setCarregandoLogout(true);

    Auth.signOut()
      .then((data) => {
        window.location.reload();
      })
      .catch((error) => {
        setCarregandoLogout(false);
      });
  };

  return (
    <>
      <Navbar bg="dark" variant="dark" expand="lg" sticky="top">
        <Container fluid>
          <Link to="/" className="m-0 p-0">
            <Navbar.Brand className="">
              <img src={NavbarLogo} width="70" alt="Logo Binx" />
            </Navbar.Brand>
          </Link>

          <Navbar.Toggle />

          {props.logged && (
            <>
              <Navbar.Collapse>
                <Nav className="mr-auto" navbarScroll>
                  {/* Dropdown de Vendas */}
                  <NavDropdown
                    title="Vendas"
                    show={vendasDrop}
                    onMouseEnter={() => setVendasDrop(true)}
                    onMouseLeave={() => setVendasDrop(false)}
                  >
                    <NavDropdown.Item as={Link} href="/fretevendas" to="/fretevendas">
                      Cálculo de Frete
                    </NavDropdown.Item>
                    <NavDropdown.Item as={Link} href="/calculomargem" to="/calculomargem">
                      Cálculo de Margem
                    </NavDropdown.Item>
                    <NavDropdown.Item as={Link} href="/prospeccao" to="/prospeccao">
                      Prospecção de Clientes
                    </NavDropdown.Item>
                  </NavDropdown>

                  {/* Dropdown de Compras */}
                  <NavDropdown
                    title="Compras"
                    show={comprasDrop}
                    onMouseEnter={() => setComprasDrop(!comprasDrop)}
                    onMouseLeave={() => setComprasDrop(false)}
                    active={comprasActive}
                  >
                    <NavDropdown.Item as={Link} href="/dashcompras" to="/dashcompras">
                      Dashboard de Compras
                    </NavDropdown.Item>
                  </NavDropdown>

                  {/* Dropdown da Expedição */}
                  <NavDropdown
                    title="Expedição"
                    show={expedicaoDrop}
                    onMouseEnter={() => setExpedicaoDrop(!expedicaoDrop)}
                    onMouseLeave={() => setExpedicaoDrop(false)}
                    active={expedicaoActive}
                  >
                    <NavDropdown.Item href="expedicao/aprovarpedidos" to="/aprovarpedidos">
                      Aprovação de Pedidos
                    </NavDropdown.Item>
                  </NavDropdown>

                  {/* Dropdown de Relatórios */}
                  <NavDropdown
                    title="Relatórios"
                    show={relatoriosDrop}
                    onMouseEnter={() => setRelatoriosDrop(!relatoriosDrop)}
                    onMouseLeave={() => setRelatoriosDrop(false)}
                    active={relatoriosActive}
                  >
                    <NavDropdown.Item href="/relatoriominmax" to="/relatoriominmax">
                      Estoque Mínimo e Máximo
                    </NavDropdown.Item>
                  </NavDropdown>

                  {/* Dropdown da Cadastros */}
                  <NavDropdown
                    title="Cadastros"
                    show={cadastrosDrop}
                    onMouseEnter={() => setCadastrosDrops(!cadastrosDrop)}
                    onMouseLeave={() => setCadastrosDrops(false)}
                    active={cadastrosActive}
                  >
                    <NavDropdown.Item href="/produtos" to="/produtos">
                      Produtos
                    </NavDropdown.Item>
                  </NavDropdown>
                </Nav>
              </Navbar.Collapse>

              <Navbar.Collapse className="justify-content-end">
                <Nav className="mr-auto" navbarScroll>
                  <BsPersonCircle />
                  <NavDropdown
                    className="dropdown-user"
                    title={
                      <div className="user-dropdown-div ">
                        <div className="m-0">
                          <BsPersonCircle size={25}/>
                        </div>
                        <div className="user-name">{userName}</div>
                      </div>
                    }
                    show={userDrop}
                    onMouseEnter={() => setUserDrop(true)}
                    onMouseLeave={() => setUserDrop(false)}
                  >
                    <NavDropdown.Item onClick={signOut}>
                      {!carregandoLogout && <>Sair</>}
                      {carregandoLogout && (
                        <div className="user-menu-sair">
                          <p>Sair</p>
                          <Spinner animation="grow" size="sm" />
                        </div>
                      )}
                    </NavDropdown.Item>
                  </NavDropdown>
                </Nav>
              </Navbar.Collapse>
            </>
          )}
        </Container>
      </Navbar>
    </>
  );
}

export default Menu;
